.container {
  max-width: none;
  width: 90%;
  margin: 0 auto;
  box-shadow: none;
}

@media screen and (min-width: 600px) {
  .container {
    margin: 5rem auto;
    max-width: 1050px;
    padding: 3rem;
    border-radius: 2rem;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
  }
}
